@import "help/vars_and_mixins";
hr {
  color: #dcdcdc;
}
.casecards {
  background: none;
  .casecards-slider {
    // height: 600px;
  }
  .join {
    position: relative;
    @include flex(flex-start, center, row);
    > a {
      position: relative;
      @include flex(start, center, row);
    }
    // margin-bottom: 120px !important;
    margin-bottom: 81px !important;
    hr {
      display: block;
      width: 100%;
      height: 1px;
      position: absolute;
      // bottom: -61px;
      bottom: -40px;
      left: 0;
      border: 0;
      background: #dcdcdc;
      color: #dcdcdc;
    }
    .join__img {
      position: relative;
      // cursor: pointer;
      width: 41.5%;
      height: 300px;
      img {
        object-fit: cover;
        border-radius: 20px;
      }
      .news-slider__block-categories {
        visibility: hidden;
        opacity: 0;
        transition: visibility 0.5s, opacity 0.5s linear;
        position: absolute;
        top: 85%;
        left: 5%;
        @media screen and (max-width: 768px) {
          top: 80%;
        }
        @media screen and (max-width: 480px) {
          // top: 80%;
          top: 73%;
          left: 0;
        }
      }
      .news-slider__categories {
        user-select: none;
        margin-bottom: 8px;
      }
      @media screen and (max-width: 1024px) {
        width: 100%;
        margin-bottom: 30px;
      }
      @media screen and (max-width: 480px) {
        display: unset;
        width: 100%;
      }
    }
    .join__img:hover {
      .news-slider__block-categories {
        visibility: visible;
        opacity: 1;
      }
    }
    .join-block {
      flex: unset;
      width: 38.58%;
      margin: 0 auto;
      padding-right: 2px;
      @media screen and (max-width: 1024px) {
        width: 100%;
        padding-right: 0;
      }
      @media screen and (max-width: 480px) {
        width: 100%;
        padding: 0;
      }
      &__title {
        font-family: "TitilliumWebBold", serif;
        cursor: pointer;
        font-size: 24px;
        line-height: 1.33;
        margin-bottom: 20px;
        width: 100%;
        text-transform: lowercase;
        span {
          font-family: "TitilliumWebRegular", serif;
        }
      }
      &__title::first-letter {
        text-transform: uppercase;
      }
      &__text {
        font-family: "TitilliumWebRegular", serif;
        font-size: 16px;
        line-height: 1.75;
        margin-bottom: unset;
        @media screen and (max-width: 480px) {
          margin-bottom: 20px;
        }
      }
    }
    a {
      color: inherit;
    }
    @media screen and (max-width: 480px) {
      .join__img {
        margin-bottom: 20px;
      }
      .join-block__title {
        font-family: "TitilliumWebSemiBold", serif;
        font-size: 18px;
        font-weight: 600;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.56;
      }
      .join-block__text {
        font-size: 14px;
        margin-bottom: unset;
      }
    }
    @media screen and (max-width: 1024px) {
      flex-direction: column;
      margin-bottom: 80px !important;
      hr {
        bottom: -40px;
      }
      .join-block__text {
        font-size: 17px;
      }
    }
  }
  .join-item {
    // margin: 0 auto !important;
    margin-left: auto !important;
    margin-right: auto !important;
    max-width: 1110px;
    // max-width: initial;
    @media screen and (max-width: 1440px) {
      max-width: 80%;
    }
    @media screen and (max-width: 1024px) {
      max-height: initial;
    }
  }
  .loader {
    padding-top: 30px !important;
    margin-bottom: 100px !important;
    #preloader {
      height: 30px;
      width: 30px;
      //   margin: 40vh auto;
      margin: 0 auto;
      border: 5px solid rgba(255, 209, 45, 0.45);
      border-top: 5px solid rgb(255, 209, 45);
      border-radius: 50%;
      animation: rotate 1s infinite linear;
    }
    @keyframes rotate {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
  }
  .hr-for-tablet {
    display: none;
  }
  @media screen and (max-width: 1024px) {
    .casecards-wrap {
      .wrapper {
        // width: 60.5%;
        width: 61.9%;
        margin-bottom: 0px !important;
        hr {
          display: none;
        }
      }
      hr.hr-for-tablet {
        display: block;
        // margin: 45px auto;
        margin: 40px auto;
        width: 90%;
        height: 1px;
        border: 0;
        background: #dcdcdc;
        color: #dcdcdc;
      }
    }
  }
  @media screen and (max-width: 480px) {
    .casecards-wrap {
      .join-item {
        max-width: initial;
      }
      .wrapper {
        width: 90.6%;
        // hr {
        //   // display: block;
        // }
      }
      hr {
        display: none;
      }
    }
  }
}
.casecards-slider {
  min-height: 400px;
  padding-top: 100px;
  &__item {
    position: relative;
    width: 1439px;
    min-height: 600px !important;
    display: flex !important;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    .main-button {
      z-index: 1;
      position: absolute;
      bottom: 11%;
      right: 10%;
      background: transparent;
      border: solid 2px #ffffff;
      color: #ffffff;
      width: 170px;
      padding: 17px 14px;
      img.black {
        display: none;
      }
      @media (min-width: 1441px) {
        right: calc((100vw - 1110px) / 2);
      }
      @media (max-width: 1024px) {
        right: 5%;
      }
      @media (max-width: 480px) {
        right: auto;
      }
    }
    .main-button:hover {
      background-color: #ffd84b;
      border: 2px solid #ffd84b;
      color: black;
      img.white {
        display: none;
      }
      img.black {
        display: block;
      }
    }
    .main-button:focus {
      background-color: #ffd84b;
      border: 2px solid #ffd84b;
      outline: none;
    }
  }
  &__item::after {
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    position: absolute;
    // background: red;
    background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.5));
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: top;
    opacity: 0.5;
  }

  &__container {
    width: 49%;
    z-index: 1;
    @media (max-width: 768px) {
      width: 88%;
    }
  }
  &__title {
    font-family: "TitilliumWebBold", serif;
    font-size: 40px;
    font-weight: bold;
    line-height: 1.15;
    text-align: center;
    color: $white;
    @media screen and (max-width: 480px) {
      font-size: 24px;
      line-height: 1.33;
    }
  }
  .slick-prev,
  .slick-next {
    position: absolute;
    z-index: 1;
    bottom: 11%;
    width: 54px;
    height: 54px;
    border: solid 2px $verylightpink;
    border-radius: 50%;
    background: transparent;
    background: url(../img/arrow-right-s-line.svg) no-repeat center;
  }
  .slick-prev:hover,
  .slick-next:hover {
    border: solid 2px $sunyellow;
    background: url(../img/arrow-right-s-line-hover.svg) no-repeat center;
    cursor: pointer;
  }
  .slick-prev {
    left: 10%;
    transform: rotate(180deg);
    @media screen and (min-width: 1441px) {
      left: calc(50% - 1100px / 2);
    }
    @media screen and (max-width: 1024px) {
      left: 5%;
    }
  }
  .slick-next {
    left: 15%;
    @media screen and (min-width: 1441px) {
      left: calc(54% - 1100px / 2);
    }
    @media screen and (max-width: 1024px) {
      left: 14%;
    }
    @media screen and (max-width: 600px) {
      left: 17%;
    }
  }
}
.case-filter {
  min-height: 140px;
  background-color: $black;
  margin-bottom: 40px;
  padding: 75px 0;
  @include flex(center, center, row);
  @media (max-width: 768px) {
    padding: 39px 0;
  }
  @media (max-width: 480px) {
    // padding: 20px 0;
    padding-top: 9px;
    padding-bottom: 20px;
  }
}

.case-filter-block {
  @include flex(space-between, flex-start, row);
  @media screen and(min-width:1024px) {
    width: 80%;
    max-width: 1110px;
    margin: 0;
  }
  @media (max-width: 768px) {
    @include flex(space-between, flex-start, column);
  }

  &__title {
    font-family: "TitilliumWebBold", serif;
    font-size: 24px;
    line-height: 1.33;
    color: #fff;
    // margin-right: 69px;
    // margin-right: 122px;
    margin-right: 10%;
    @media (max-width: 768px) {
      display: none;
    }
    @media (min-width: 700px) and (max-width: 1231px) {
      margin-right: 3%;
    }
  }
  &__tabs {
    width: 51.354%;
    @include flex(space-between, flex-start, row);
    @media (max-width: 768px) {
      margin-bottom: 30px;
      width: 73%;
    }
    @media (max-width: 480px) {
      flex-direction: column;
      align-items: flex-start;
    }
    @media (min-width: 480px) and (max-width: 750px) {
      width: 100%;
    }
  }
  &__item {
    width: 100%;
    position: relative;
    cursor: pointer;
    font-family: "TitilliumWebSemiBold", serif;
    line-height: 1.5;
    font-size: 16px;
    // color: $browngrey;
    color: #ffff;
    padding: 0 11px;
    // margin-right: 18px;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    @media (max-width: 600px) {
      margin-right: 5px;
    }
    @media (max-width: 480px) {
      margin-bottom: 15px;
      padding: 11px;
      margin-right: 18px;
    }
    @media screen and(max-width:768px) {
      padding: 0 17px;
      margin-right: 18px;
    }
  }
  &__list {
    font-family: "TitilliumWebRegular", serif;
    font-size: 14px;
    line-height: 1.71;
    color: #fff;
    position: relative;
    margin-top: 5px;
    img {
      position: absolute;
      left: -15px;
      top: 9px;
    }
  }
  &__list-insert {
    display: none;
    background-color: $black;
    position: absolute;
    // padding: 5px 30px;
    padding: 10px 30px;
    z-index: 1;
    left: -18px;
  }
  &__item-insert {
    font-family: "TitilliumWebRegular", serif;
    font-size: 14px;
    line-height: 1.71;
    color: #fff;
    margin-bottom: 10px;
  }
  &__item-insert:hover {
    color: $sunyellow;
  }
  .search-form {
    margin-left: auto;
    padding: 0 24px;
    @media (max-width: 768px) {
      margin-left: 0;
    }
  }
}
